<template>
	<div id="favorite">
		<public-header title="我的收藏"></public-header>
		<public-footer :menufooter="menufooter"></public-footer>
		<div class="content">
			<van-list v-model="stores.loading" :finished="stores.finished" @load="onLoad" v-if="stores.data.length > 0" :offset="100" :immediate-check="false">
				<div class="diy-haodian-list" >
					<div class="store-item " :class="{'van-hairline--top': haodianIndex > 0}" v-for="(haodianItem, haodianIndex) in stores.data" :key="haodianIndex">
						<router-link tag="div" :to="util.getUrl({path: '/gohome/pages/haodian/detail', query: {sid: haodianItem.id}})" class="img-wrap">
							<img :src="haodianItem.logo" alt="">
						</router-link>
						<div class="store-main">
							<router-link tag="div" :to="util.getUrl({path: '/gohome/pages/haodian/detail', query: {sid: haodianItem.id}})">
								<div class="store-title">{{haodianItem.title}}</div>
								<div class="flex-lr">
									<div class="flex">
										<van-rate v-model="haodianItem.haodian_score" :size="12" disabled-color="#ff2d4b" disabled />
										<span class="c-gray font-12 margin-5-l">{{haodianItem.haodian_score}}分</span>
									</div>
								</div>
								<div class="c-gray font-12 margin-10-t">营业时间: {{haodianItem.business_hours_cn}}</div>
								<ul class="store-tags" v-if="haodianItem.haodian_tags && haodianItem.haodian_tags.length > 0">
									<li class="tag-item" v-for="(haodianTag, haodianTagIndex) in haodianItem.haodian_tags" :key="haodianTagIndex">{{haodianTag}}</li>
								</ul>
							</router-link>
							<div class="discount-box" v-if="haodianItem.activity &&  haodianItem.activity.length > 0">
								<div class="single-line" v-for="(activityItem, activityIndex) in haodianItem.activity" :key="activityIndex">
									<img class="discount-icon" :src="activityItem.thumb_vue">
									<div class="discount-text">{{activityItem.text}}</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</van-list>
			<div v-else class="no-data">
				<img src= "static/img/collect_no_bg.png" alt="" />
				<p>您没有收藏</p>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import PublicFooter from '@/components/footer'
	export default {
		data() {
			return {
				stores: {
					min: 0,
					loading: false,
					finished: false,
					data: []
				},
				menufooter: {},
				showPreLoading: true
			}
		},
		components: {
			PublicHeader,
			PublicFooter,
		},
		methods: {
			onLoad() {
				if(this.stores.finished) {
					return false;
				}
				this.util.request({
					url: 'haodian/index/favorite',
					data: {
						min: this.stores.min,
						menufooter: 1
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					this.stores.data = [...this.stores.data, ...result.message];
					this.stores.loading = false;
					this.stores.min = result.min;
					if(result.message.length < 10 || !result.min) {
						this.stores.finished = true;
					}
					this.menufooter = window.menufooter;
				})
			},
			onToggleActivity(index) {
				this.stores.data[index]['activity']['is_show_all'] = !this.stores.data[index]['activity']['is_show_all'];
				this.stores.data[index]['activity']['items'] = [...this.stores.data[index]['activity']['items']];
			},
		},
		mounted () {
			this.onLoad();
		},
	}
</script>

<style>
	@import "../../../../static/css/diy.css";
</style>


